.friend-list {
  display: flex;
  align-items: center;
  flex-direction: column;

  .item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 400px;
    border: solid 0.5px indigo;
    border-radius: 5px;
    padding: 5px;
    margin: 5px;
  }
}
